
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import sc from 'vux/src/components/scroller/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import Marquee from 'vux/src/components/marquee/marquee.vue'
import MarqueeItem from 'vux/src/components/marquee/marquee-item.vue'
import Panel from 'vux/src/components/panel/index.vue'


var start=0;
var end=start+9;

var key=0
var refresh=['A','B01','B02','B03','B04','B05','B06','B07','B08','B09']
var ref='A'
var refreshFlag=false;
function getRefresh(){
ref=refresh[key];
key++;
if(key === refresh.length){
key=0
}
return ref
}

export default {
name: 'App',
components: {
ViewBox,XHeader,Tabbar,TabbarItem,Tab,TabItem,sc,Swiper,Marquee, MarqueeItem,Panel
},
created(){
var url='https://3g.163.com/touch/jsonp/sy/recommend/0-9.html'
this.$http.jsonp(url).then( (data) => {
this.swiperList=data.body.focus.filter( (item) => {
return item.addata===null && item.picInfo[0]
}).map((item)=>{
return {
url: item.link,
img: item.picInfo[0].url,
title:item.title
}
})
this.dataList=data.body.list.filter( (item) => {
return item.addata===null && item.picInfo[0]
}).map((item)=>{
return {
url:item.link,
src: item.picInfo[0].url,
title: item.title,
desc: item.digest
}
})
this.marqueeList=data.body.live.filter( (item) => {
return item.addata===null && item.picInfo[0]
}).map((item)=>{
return {
title:item.title,
url:item.link
}
})
refreshFlag=true;
this.$refs.ref.finishInfinite()
})
},
data(){
return {
tabList:['推荐','新闻','娱乐','体育','图片','财经','汽车','直播'],
swiperList:[],
marqueeList:[],
dataList:[],
moreData:[]
}
},
methods:{
refresh(){
var url=`https://3g.163.com/touch/jsonp/sy/recommend/0-9.html`
this.$http.jsonp(url,{
params:{
miss:'00',
refresh:getRefresh()
}
}).then( (data) => {
start=0
setTimeout( () => {
this.dataList=data.body.list.filter( (item) => {
return item.addata===null && item.picInfo[0]
}).map((item)=>{
return {
url:item.link,
src: item.picInfo[0].url,
title: item.title,
desc: item.digest
}
})
this.$refs.ref.finishPullToRefresh()
},500)
})
this.moreData=[]
},
infinite(){
start+=10
if(!refreshFlag){return}
var url=`https://3g.163.com/touch/jsonp/sy/recommend/${start}-${end}.html`
this.$http.jsonp(url,{
params:{
miss:'00',
refresh:ref
}
}).then( (data) => {
console.log(start,ref)
var data=data.body.list.filter( (item) => {
return item.addata===null && item.picInfo[0]
}).map((item)=>{
return {
url:item.link,
src: item.picInfo[0].url,
title: item.title,
desc: item.digest
}
})
this.moreData=this.moreData.concat(data)
this.$refs.ref.finishInfinite()
})
}
}
}



