import Vue from 'vue'
import App from './App'
import router from './router'
import VueResource from 'vue-resource'
import VueScroller from 'vue-scroller'
import store from './store'
Vue.use(VueScroller)
Vue.use(VueResource)
Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  
  store,
  components: { App },
  template: '<App/>'
})



